CA,wCAAA;CPg1BP;;AO91BD;EAiBQ,gBAAA;EACA,eAAA;EACA,mBAAA;EACA,WAAA;EACA,WAAA;EACA,kBAAA;CPi1BP;;AOv2BD;EAyBQ,uCAAA;CPk1BP;;AO32BD;EA4BQ,eAAA;EACA,wCAAA;CPm1BP;;AO/0BD;EAEI,iBAAA;EACA,eAAA;EACA,eAAA;CPi1BH;;AQ5/BD;EACI,uBAAA;EACA,aAAA;EACA,gBAAA;CR+/BH;;AQlgCD;EAMQ,iBAAA;EACA,0BAAA;EACA,0BAAA;EACA,qCAAA;EACA,wCAAA;CRggCP;;AQ1gCD;EAaQ,YAAA;EACA,iBAAA;CRigCP;;AQ/gCD;EAgBY,YAAA;EACA,iBAAA;EACA,gBAAA;CRmgCX;;AQrhCD;EAoBgB,YAAA;EACA,iBAAA;EACA,wCAAA;CRqgCf;;AQ3hCD;EAwBoB,oBAAA;CRugCnB;;AQ/hCD;EA2BoB,eAAA;CRwgCnB;;AQniCD;EA8BoB,gBAAA;CRygCnB;;AQviCD;EAoCQ,eAAA;CRugCP;;AQ3iCD;EAuCQ,YAAA;EACA,iBAAA;EACA,qCAAA;CRwgCP;;AQjjCD;EA2CY,gBAAA;EACA,WAAA;EACA,sBAAA;EACA,YAAA;CR0gCX;;AQxjCD;EAiDY,sBAAA;EACA,mBAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;EACA,WAAA;EACA,0BAAA;EACA,gBAAA;EACA,YAAA;CR2gCX;;AQpkCD;EA4DY,kBAAA;CR4gCX;;AQxkCD;EAgEQ,wCAAA;EACA,gBAAA;CR4gCP;;AQ7kCD;EAoEQ,YAAA;EAGA,iBAAA;EACA,sBAAA;EAAA,cAAA;CR2gCP;;AQnlCD;EA4EY,WAAA;EACA,kBAAA;EACC,uBAAA;UAAA,eAAA;EACD,gBAAA;EACA,mBAAA;EACA,kBAAA;EACA,wCAAA;CR2gCX;;AQ7lCD;EAsFY,gBAAA;EACA,eAAA;EACA,mBAAA;EACA,WAAA;EACA,WAAA;EACA,kBAAA;CR2gCX;;AQtmCD;EA8FY,uCAAA;CR4gCX;;AQ1mCD;EAiGY,eAAA;EACA,wCAAA;CR6gCX;;AQ/mCD;EAsGQ,iBAAA;EACA,eAAA;CR6gCP","file":"CV.scss","sourcesContent":["@import 'common/reset.scss';\n@import 'common/common.scss';\n@import 'index.scss';\n@import 'header.scss';\n@import 'about.scss';\n@import 'topic.scss';\n@import 'user.scss';\n@import 'detail.scss';\n","/**\n * Global Reset of all HTML Elements\n *\n * Resetting all of our HTML Elements ensures a smoother\n * visual transition between browsers. If you don't believe me,\n * try temporarily commenting out this block of code, then go\n * and look at Mozilla versus Safari, both good browsers with\n * a good implementation of CSS. The thing is, all browser CSS\n * defaults are different and at the end of the day if visual\n * consistency is what we're shooting for, then we need to\n * make sure we're resetting all spacing elements.\n *\n */\nhtml, body {\n    height: 100%;\n    border: 0;\n    font-family: \"Helvetica-Neue\", \"Helvetica\", Arial, sans-serif;\n    line-height: 1.5;\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\ndiv, span, object, iframe, img, table, caption, thead, tbody,\ntfoot, tr, tr, td, article, aside, canvas, details, figure, hgroup, menu,\nnav, footer, header, section, summary, mark, audio, video {\n    border: 0;\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nh1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, address, cit, code,\ndel, dfn, em, ins, q, samp, small, strong, sub, sup, b, i, hr, dl, dt, dd,\nol, ul, li, fieldset, legend, label {\n    border: 0;\n    font-size: 100%;\n    vertical-align: baseline;\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\narticle, aside, canvas, figure, figure img, figcaption, hgroup,\nfooter, header, nav, section, audio, video {\n    display: block;\n    box-sizing: border-box;\n}\n\ntable {\n    border-collapse: separate;\n    border-spacing: 0;\n    caption, th, td {\n        text-align: left;\n        vertical-align: middle;\n    }\n}\nli{\n    list-style: none;\n}\na{\n    text-decoration: none;\n}\na img {\n    border: 0;\n}\n\n:focus {\n    outline: 0;\n}\n\n\ntextarea{\n    resize: none;\n    appearance: none;\n    box-sizing: border-box;\n}\ninput{\n    appearance: none;\n    box-sizing: border-box;\n}\nselect {\n    appearance: none;\n    background-color: #fff;\n    box-sizing: border-box;\n}\n","$color31:#313131;\n$colorf0:#f0f0f0;\n$colorfff:#fff;\n$colore7:#e7e7e7;\n$colorfa:#fafafa;\n$color62:#626262;\n$colord4:#d4d4d4;\n$colora0:#a0a0a0;\n$red:#ff5a5f;\n$yellow:#feb501;\n$color80:#80bd01;\n$gap:5PX;\n\nbody {\n    height: 100%;\n    width: 100%;\n    font-size: 14px;\n    color: #313131;\n    overflow-x: hidden;\n    line-height: 1;\n    background: url('../images/loading.gif') no-repeat center 250px;\n}\n\n.mt5 {\n    margin-top: $gap;\n}\n\n.mt10 {\n    margin-top: $gap*2;\n}\n\n.color80 {\n    color: #42b983;\n}\n\n.clear {\n    zoom: 1;\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n#app {\n    height: 100%;\n}\n\n.display-flex {\n    display: -moz-box;\n    /* Firefox */\n    display: -ms-flexbox;\n    /* IE10 */\n    display: -webkit-box;\n    /* Safari */\n    display: -webkit-flex;\n    /* Chrome, WebKit */\n    display: flexbox;\n    display: flex;\n}\n\n.break {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.user {\n    width: 100%;\n    margin: $gap*2 0;\n    padding: 0 $gap*2;\n    display: flex;\n    .tab {\n        display: inline-block;\n        width: $gap*8;\n        color: $color62;\n        border-radius: $gap;\n        background-color: $colore7;\n        margin-right: $gap*2;\n        text-align: center;\n        height: $gap*4;\n        line-height: $gap*4;\n        vertical-align: middle;\n    }\n    .good {\n        background-color: $color80;\n        color: $colorfff;\n    }\n    .title {\n        font-size: $gap*3.6;\n        font-weight: bold;\n        display: block;\n        width: 100%;\n        flex: 1;\n        @extend .break;\n    }\n    .head {\n        display: inline-block;\n        width: $gap*9;\n        height: $gap*9;\n        margin-right: $gap*2;\n        border-radius: 10px;\n        img {\n            width: $gap*9;\n            border: 2px solid #fff6e6;\n            border-radius: 50%;\n        }\n    }\n    .info {\n        overflow: hidden;\n        display: block;\n        width: 100%;\n        flex: 1;\n        &:after {\n            clear: both;\n        }\n        .t-title {\n            font-size: $gap*3.6;\n            font-weight: bold;\n            width: 100%;\n            color: #333;\n            @extend .break;\n        }\n        .mt12 {\n            margin-top: 12px;\n        }\n        .cl {\n            display: inline-block;\n            width: 68%;\n            .name {\n                color: $color62;\n            }\n            .mt10 {\n                margin-top: $gap*2;\n            }\n        }\n        .cr {\n            width: 30%;\n            display: inline-block;\n            text-align: right;\n            .name {\n                margin-top: $gap*2;\n                color: $color80;\n                font-size: $gap*2.4;\n            }\n        }\n    }\n}\n\n.reply_content {\n    padding: 0 $gap*3;\n    margin-bottom: $gap*3;\n    img {\n        width: auto\\9;\n        height: auto;\n        max-width: 100%;\n        vertical-align: middle;\n        border: 0;\n        -ms-interpolation-mode: bicubic;\n    }\n    p {\n        line-height: 1.3;\n    }\n}\n\n.no-data {\n    width: 100%;\n    height: 100%;\n    padding: 40% 0;\n    color: $colord4;\n    display: inline-block;\n    font-size: 18PX;\n    text-align: center;\n}\n\n\n/*\n**   rework åççº¿\n**   ------------\n*/\n\n\n/* é¢è² */\n\n$white: #ffffff;\n$light: #d5dbdb;\n$title: #2c3e50;\n$text: #34495e;\n\n/* åºç¡å¸å± */\n\n$padding: 15PX;\n$border: 1PX solid $light;\n$radius: 4PX;\n$font-title: 16PX;\n$font-content: 14PX;\n$font-info: 12PX;\n$font-tag: 10PX;\n\n/* ç»¼åå¸å± */\n\n.text-overflow {\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow: hidden;\n}\n\n.title-nooverflow {\n  color: $title;\n  font-size: $font-title;\n  line-height: 1.5;\n}\n\n.title {\n    color: $title;\n    font-size: $font-title;\n    line-height: 1.5;\n    @extend .text-overflow;\n}\n\n.markdown-text {\n  font-size: 28px;\n}\n\n\n\n.tag {\n    padding: 5px 6px;\n    font-size: $font-tag;\n    font-weight: 400;\n    border-radius: $radius;\n    background-color: $colore7;\n    text-align: center;\n    vertical-align: middle;\n}\n\n.user-avatar {\n    display: block;\n    width: 40Px;\n    height: 40Px;\n    border-radius: 50%;\n    margin-right: 10Px;\n    border: 1px solid #F3F3F3;\n}\n\n.week-alert {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    text-align: center;\n    background-color: rgba(75, 75, 75, 0.85);\n    color: #fff;\n    padding: 9px 20px;\n    font-size: 15px;\n    z-index: 999;\n    &.alert-show {\n        animation: show 2s;\n    }\n    @keyframes show {\n        0% {\n            transform: translateY(-100%);\n        }\n        20% {\n            transform: translateY(0);\n        }\n        50% {\n            transform: translateY(0);\n        }\n        80% {\n            transform: translateY(0);\n        }\n        100% {\n            transform: translateY(-100%);\n        }\n    }\n}\n","#brim-mask {\n    pointer-events: none;\n}\n\n#page {\n    padding-top: 100px;\n    background-color:#fff;\n    transition: all .3s ease;\n    // overflow-x:hidden;\n}\n\n.show-menu {\n    transform: translateX($gap*40);\n}\n\n.page-cover {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background: rgba(0, 0, 0, .4);\n    z-index: 7;\n}\n\n\n/*æ¨¡åå¥å£æ ·å¼*/\n\n.posts-list {\n    background-color: $white;\n    .topic {\n        padding: 20px $padding;\n        border-bottom: $border;\n    }\n    .stitle {\n        @extend .title;\n        &:before {\n            content: attr(title);\n            margin-right: 10PX;\n            margin-top: -3px;\n            @extend .tag;\n            color: $white;\n        }\n        &.top:before {\n            background: #E74C3C;\n        }\n        &.ask:before {\n            background: #3498DB;\n        }\n        &.good:before {\n            background: #E67E22;\n        }\n        &.job:before {\n            background: #9B59B6;\n        }\n        &.share:before {\n            background: #1ABC9C;\n        }\n    }\n    .content {\n        padding-top: 10Px;\n        display: flex;\n    }\n    .avatar {\n       @extend .user-avatar;\n    }\n    .info {\n        display: block;\n        width: 100%;\n        flex: 1;\n\n        div {\n            padding: 3px 0;\n            display: flex;\n            color: $text;\n      